import { getMutiChairs, getTileImageString, getHun, getChiBrand, changeThree } from './actionSumFun.js'
import * as types from './actionTypes'

var actionDesc = {
  [types.CHI]: action => `: [${getMutiChairs(action[1])}]  ${getChiBrand(action[2], action[4])}  [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.PENG]: action => `: [${getMutiChairs(action[1])}] [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.ZHIGANG]: action => `: [${getMutiChairs(action[1])}]  [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.ANGANG]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}`,
  [types.ZHUANGANG]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}`,
  [types.HU]: action => {
    var chairs = getMutiChairs(action[1])
    return '[' + chairs + '] [' + getMutiChairs(action[3]) + '] ' + getTileImageString(action[2]) + ' ' + (chairs.length > 1 ? ', ' : '')
  },
  [types.ZIMO]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])} `,
  [types.DISCARD]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}`,
  [types.DRAW]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}`,
  [types.DEALER]: action => `:  [${getMutiChairs(action[1])}]  [${Math.floor(action[4] / 10)}, ${action[4] % 10}]`,
  [types.FOLLOWDEALER]: action => ': ',
  [types.HUAN3]: action => ':' + changeThree(action),
  [types.DINGQUE]: action => '//  ',
  [types.PAOZI]: action => {
    var result = '/: '
    for (var i = 1; i < 5; i++) {
      if (action[i] !== 0) result += '[' + i + '] :' + action[i] + ', '
    }
    return result
  },
  [types.FANHUN]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}${getTileImageString(getHun(action[2]))}`,
  [types.DRAW4]: action => {
    var result = '/4: '
    for (var i = 1; i < 5; i++) {
      if (action[i] !== 0) result += `[${(i - 1)}] ${getTileImageString(action[i])},`
    }
    return result
  },
  [types.MINGLOU]: action => `: [${getMutiChairs(action[1])}] `,
  [types.XIAOSA]: action => `: [${getMutiChairs(action[1])}] `,
  [types.ZHUANIAO]: action => {
    var niaoNum = 0
    var mutiNames = 0
    for (var i = 2; i < 5; i++) {
      mutiNames = mutiNames * 10000 + action[i]
    }
    var remain = mutiNames
    while (remain > 0) {
      remain % 100 && niaoNum++
      remain = Math.floor(remain / 100)
    }

    return `${niaoNum}: ${getTileImageString(mutiNames)}`
  },
  [types.ZA]: action => `: [${getMutiChairs(action[1])}] ()[${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.ZAGANG]: action => `: [${getMutiChairs(action[1])}]  [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.ZAANGANG]: action => `: [${getMutiChairs(action[1])}] ${getTileImageString(action[2])}`,
  [types.BIAN]: action => `: [${getMutiChairs(action[1])}]  ${getChiBrand(action[2], action[4])} () [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.ZUAN]: action => `: [${getMutiChairs(action[1])}]  ${getChiBrand(action[2], action[4])} () [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.KOUCARD]: action => {
    var result = '4: '
    for (var i = 1; i < 5; i++) {
      if (action[i] !== 0) result += `[${(i - 1)}] ${getTileImageString(action[i])},`
    }
    return result
  },
  [types.YIKOUXIANG]: action => `: [${getMutiChairs(action[1])}] `,
  [types.QIANGGANGHU]: action => `: [${getMutiChairs(action[1])}] [${getMutiChairs(action[3])}] ${getTileImageString(action[2])}`,
  [types.TUIDABING]: action => {
    var result = ': '
    for (var i = 1; i < 5; i++) {
      if (action[i] !== 0) result += `[${(i - 1)}] ${getTileImageString(action[i])},`
    }
    return result
  },
  [types.GUOPAI]: action => `: [${getMutiChairs(action[1])}] `
}

export default actionDesc



