<template>
  <div class="container">
    <md-layout md-gutter>
      <md-layout md-flex="50" md-flex-offset="25">
        <div>
          <b></b>
        </div>
        <md-input-container 
          :class="uploadFile || 'md-input-invalid'">
          <md-file
            required
            @selected="checkFileSuffix"
            placeholder=""></md-file>
            <span class="md-error">ttf</span>
        </md-input-container>
        <md-input-container 
          md-clearable
          :class="filterText || 'md-input-invalid'">
          <label></label>
          <md-textarea 
            required
            v-model="filterText"></md-textarea>
          <span class="md-error"></span>
        </md-input-container>
        <!-- <md-layout>
          <md-radio 
            v-model="filterType" 
            name="filter-radio" 
            md-value="1"
            class="md-primary"></md-radio>
          <md-radio 
            v-model="filterType" 
            name="filter-radio" 
            md-value="0"></md-radio>
        </md-layout> -->
        <md-layout md-getter>
            <md-button
              v-if="!sending"
              :disabled="sending || uploadFile === null || filterText === ''"
              @click="zip"
              class="md-raised md-primary">Zip!</md-button>
            <md-layout md-align="center">
              <md-spinner 
                v-if="sending"
                :md-size="30" md-indeterminate></md-spinner>
            </md-layout>
            <a
              v-if="downloadUrl != '0'"
              :href="downloadUrl"
              class="md-raised md-button md-accent md-theme-default">!</a>
        </md-layout>
        
      </md-layout>
    </md-layout>
  </div>
</template>
<script>
import http from '../utils/http'
import router from '../router'

export default {
  data () {
    return {
      sending: false,
      uploadFile: null,
      filterText: '1234567890abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ',
      filterType: 1,
      downloadUrl: this.$route.params.download
    }
  },
  methods: {
    checkFileSuffix (fileList) {
      var file = fileList[0]
      var suffix = file.name.match(/(?=.)\w+$/)
      if (!/ttf/.test(suffix)) {
        alert(' ttf')
        return
      }
      this.uploadFile = file
    },
    zip () {
      this.sending = true
      this.downloadUrl = '0'
      var params = {
        type: parseInt(this.filterType),
        filterText: this.filterText,
        fontFile: this.uploadFile
      }
      http.post('http://192.168.1.48:3000' + '/zipfont', params)
      .then(data => {
        this.downloadUrl = data.download
        router.push({ path: '/zipfont/' + encodeURIComponent(data.download) })
      })
      .catch(e => console.log('' + e))
      .then(() => (this.sending = false))
    }
  }
}
</script>
<style>

</style>




